.sc-tf-inter-model {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
  .sc-tf-inter-box {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
  }
  .overflow-box {
    position: absolute;
    left: 0;
    top: 0;
    overflow: hidden;
    width: auto;
    height: 100%;
    z-index: 2;
  }
  .sc-tf-phase-box {
    // position: absolute;
    // left: 0;
    // top: 0;
    // z-index: 2;
    width: calc(100% + 20px);
    // background-color: #fff;
    height: 100%;
    overflow: auto;
    display: flex;
    flex-direction: column;
  }
}
