import { App } from 'vue'
import tfInterModel from './interModel'
 
// 
const components = [ tfInterModel ]
 
//  install  App 
const install = (app: App): void => {
    // 
    components.map((component) => app.component(component.name, component))
}
 
export {
  tfInterModel
}
 
export default {
    install
}